.appartmentCountainer {
    width: 80%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

.placeSelected,
.placeUnselected {
    width: 40%;
    height: 12em;
    margin: 1em;
    border: 1px solid gray;
    border-radius: 8px;
    padding: 1em;
    text-align: center;
    background-color: white;
    transition: .1s;
}

.appartmentCountainer input {
    vertical-align: middle;
    font-size: 2em;
    width: 3em;
    margin-top: 1.5em;
    border: 1px solid gray;
    border-radius: 3px;
    padding-left: 9px;
}

.appartmentCountainer h1 {
    margin-top: 1.5em;
}

.placeUnselected:hover,
.placeSelected {
    transform: scale(1.05);
    cursor: pointer;
    background-color: rgb(184, 255, 231);
}