.appointmentCard {
    background-color: inherit;
    border-radius: 5px;
    text-align: center;
    font-size: 1.75em;
    padding: 3px;
    width: 100%;
    height: 100%;
    border: 1px solid gray;
    border-radius: 8px;
    transition: .1s;
}

.appointmentCard:hover {
    transform: scale(1.05);
    cursor: pointer;
    background-color: rgb(184, 255, 231);
}

input[type=radio] {
    visibility: hidden;
}

input[type=radio]:checked+label {
    background-color: rgb(184, 255, 231);
    transform: scale(1.05);
}