.appointmentContainer {
    border-radius: 5px;
    border-width: 2px;
    border-color: navy;
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
}

.appointmentInput {
    width: 12em;
    height: 6em;
    margin: 3px;
    //flex: 1;
}

#bloc2 {
    margin-top: 2em;
}

.bloc2unslid {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-5rem);
}

.bloc2slid {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
    transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}