#bloc3 {
    margin-top: 2em;
}

.bloc3unslid {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-5rem);
}

.bloc3slid {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
    transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}